          "></router-link>
          <router-link class="link-profile"
              :to="{ name: 'PersonalEditEducation',
              params: { username: this.user.username }}
          "></router-link>

        </div>
      </div>

      <div v-if="loading" class="loading-main">
        <img src="/img/preloader.svg" alt=" ">
      </div>

      <el-form v-else>
        <div class="row-group row-group-profile">

         <div class="column-group-profile">

          <el-form-item prop="last_name">
            <label class="required-label label" for="last_name"></label>
            <el-input id="last_name" type="text" class="input" placeholder=" " v-model="formData.last_name"></el-input>
          </el-form-item>

