              v-model="formData.date_birth"
              style="width: 100%;"
            ></el-date-picker>
          </el-form-item>

          <el-form-item prop="tel">
            <label class="label" for="tel"></label>
            <el-input type="text" id="tel" class="input" placeholder=" " v-model="formData.phone"></el-input>
          </el-form-item>

          <el-form-item prop="email">
            <label class="label" for="email">E-mail</label>
            <el-input type="text" id="email" class="input" placeholder=" " v-model="formData.email"></el-input>
          </el-form-item>

          <div class="column-form">
            <label class="label" for="bac-grade"></label>
            <el-select id="bac-grade" placeholder="" v-model="formData.gender">
              <el-option value="male" label="">
              </el-option>
              <el-option value="female" label="">
              </el-option>
            </el-select>
          </div>

          <el-form-item prop="image">
